















































































@import "../styles/colors";

.todo {
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding: 1.25rem;

  &--checked .todo__description {
    text-decoration: line-through;
  }

  &__actions {
    display: flex;

    &__delete {
      fill: $pink;
      margin-right: 1.25rem;
      width: 1rem;
    }
  }

  &__description {
    margin: 0;
  }
}
