

























































@import "../styles/colors";

.todo {
  flex: 1;
}

.todo-title {
  color: $white;
}

.todo-add {
  display: flex;

  &__controls {
    display: flex;
    margin-left: auto;
  }

  button {
    background-color: $pink;
    border-radius: 0.25rem;
    border: none;
    color: $white;
    font-weight: bold;
    margin-left: 0.75rem;
    padding: 0.75rem 1rem;
  }
}

.todo-container {
  background-color: $white;
  border: 2px solid $blue;
  border-radius: 1.25rem;
  margin-bottom: 1.75rem;
}

.todo-empty {
  padding: 1.25rem;
}
